import "./styles.css";

import duck_sound from "./sound/duck_sound.wav";
import bg from "./image/bj.jpg";

const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
    maxWidth: 300,
    borderRadius: 15
  }
};

function Paperella() {
  let sound = new Audio(duck_sound);
  function playSound() {
    sound.play();
    const timer = setTimeout(() => {
      window.open(
        "https://www.eventbrite.it/e/biglietti-falling-stars-pool-party-166376539709"
      );
      clearTimeout(timer);
    }, 500);
  }
  return (
    <div className="Paperella">
      <a onClick={() => playSound()} className="PaperellaLink">
        FAI CLICK QUI PER UN BIGLIETTO
      </a>
    </div>
  );
}

